<script>
	import Header from './common/Header.vue'
	import {mapGetters , mapActions} from 'vuex'
	import ResumeItem from './common/ResumeItem.vue'
	export default {
	  name:'ResumeList',
	  components:{
	     'common-header': Header,
	     'resume-item': ResumeItem
	  },
	  data () {
	    return {
	    	titleName:'北京总复试第一批',
	    	timeSlot:'09:00-09:20',
	    	isShowSearchText:false,
	    	pageIndex:1,
	    	pageSize: 5,
	    	roomId: this.$route.query.roomId,
	    	signStatus: 0
	    }
	  },
	  created(){
	  	const {pageIndex , pageSize , roomId , signStatus} = this;
	  	this.getExamDetailList({pageIndex , pageSize , roomId , signStatus});
	  },
	  computed:{
	  	...mapGetters(['examDetailList'])
	  },
	  methods:{
	  	...mapActions(['getExamDetailList']),
	    clickTab(flag){
	    	this.isNotActive = flag;
	    	this.signStatus = flag;
	    	const {pageIndex , pageSize , roomId , signStatus} = this;
	  		this.getExamDetailList({pageIndex , pageSize , roomId , signStatus});
	    },
	    clickSearch(flag){
	    	this.isShowSearchText = flag;
	    },
	    changeNextPageStart(ev){
	    },
	    changeNextPageMove(ev){
	    },
	    changeNextPageEnd(ev){
	    	const eleScrollTop = window.document.documentElement.scrollTop;
	    	const eleScrollHeight = window.document.documentElement.scrollHeight;
	    	const eleClientHeight = window.document.documentElement.clientHeight;
	    	if (eleScrollTop + eleClientHeight === eleScrollHeight) {
	    		this.pageIndex++;
	    		const {pageIndex , pageSize , roomId , signStatus} = this;
	    		this.getExamDetailList({pageIndex , pageSize , roomId , signStatus});
	    	}
	    },
	    toSearchRecord(){

	    },
	    toResumeList(){

	    }
	  }
	}
</script>
<template>
	<div class="resume-list" ref="list">
		<common-header :title="titleName"></common-header> 
		<div class="search-tab clearfix" :class='isShowSearchText ? "hidden-div" : ""'>
			<div class="alltab" :class="signStatus === 0 ? 'active' : ''" @click="clickTab(0)">全部</div>
			<div class="notsign" :class="signStatus === -1  ? 'active' : ''" @click="clickTab(-1)">
				未签到
				<span>{{examDetailList.noSign}}</span>
			</div>
			<i class="fl" @click="clickSearch(true)"></i>
		</div>
		<div class="search-text clearfix" :class='isShowSearchText ? "" : "hidden-div"'>
			<div class="text fl">
				<i class="fl"></i>
				<input type="text" @focus="toSearchRecord" @blur="toResumeList">
			</div>
			<div class="cancel fl" @click="clickSearch(false)">取消</div>
		</div>
		<div class="tip">{{timeSlot}}</div>
		<div class="content" @touchstart="changeNextPageStart($event)" @touchmove="changeNextPageMove($event)"
      @touchend="changeNextPageEnd($event)">
			<!-- <resume-item v-for="(item , order) in examDetailList.data" :item="item" :order="order + 1"/> -->
			<div class="item"></div>
			<div class="item"></div>
			<div class="item"></div>
			<div class="item"></div>
			<div class="item"></div>
			<div class="item"></div>
			<div class="item"></div>
			<div class="item"></div>
			<div class="item"></div>
		</div>
	</div>
</template>
<style lang='less' scoped>
	@import '../assets/css/resumeList.less';
</style> 


