<script>
	export default{
		name:'ResumeItem',
		data(){
			return{
				imgPathObj:{
		    		0: require('../../assets/images/undetermined.png'),
		    		1: require('../../assets/images/pass.png'),
		    		2: require('../../assets/images/noPass.png'),
    			}
			}	
    	},
    	props:{
    		item:{
    			type:Object,
    			default(){
    				return {}
    			}
    		},
    		order:{
    			type:Number,
    			default(){
    				return 0;
    			}
    		}
    	}
	}
</script>
<template>
	<div class="item clearfix">
		<img src="item.PhotoSrc" class="fl">
		<div class="fl item-left">
			<div class="person-info-top">
				<span class="name">{{item.Name}}</span>
				<span class="sex">{{item.Gender}}</span>
				<span class="education">{{item.Degree}}</span>
				<span class="school-level">{{item.SchoolType}}</span>
				<span class="sign">{{item.SignStatus === 1 ? '已签到' : '未签到' }}</span>
				<span class="order">{{order + 1}}</span>
			</div>
			<div class="person-info-middle">
				<span>{{item.SchoolName}}</span>
				<span>|</span>
				<span>{{item.Major}}</span>
			</div>
			<div class="person-info-bottom clearfix" v-if="item.SignStatus !== 1">
				<span class="fl">{{item.CommentCount}}</span>
				<div class="contact fl clearfix">
					<a class="fl" :href="'tel:' + item.Phone"></a>
					<a class="fl" :href="'sms:' + item.Phone"></a>
				</div>
			</div>
		</div>
		<img :src="imgPathObj[item.SiftStatus]" class="pass-flag">
	</div>
</template>
<style lang="less" scoped>
	@r:40rem;
	.item{
		position: relative;
		background-color: white;
		padding:30/@r 0 30/@r 30/@r;
		color: #99a5a7;
		font-size: 20/@r;
		margin-bottom: 15/@r;
		img{
			width: 120/@r;
		}
		.item-left{
			margin-left: 25/@r;
			.person-info-top{
				margin-bottom: 40/@r;
				font-size: 24/@r;
				.name{
					font-size: 28/@r;
					font-weight: 600;
					margin-right: 10/@r;
					color: black;
				}
				.sex,.education,.school-level{
					color: #707c7f;
					font-size: 24/@r;
				}
				.sign{
					color: #ff3600;
					border:1px solid #ff3600;
					padding: 0 8/@r;
						border-radius: 15/@r;
					font-size: 20/@r;
				}
				.order{
					margin-left: 60/@r;
					color: #f29c00;
				}
			}
			.person-info-middle{
				margin-bottom: 35/@r;
			}
			.person-info-bottom{
				.contact{
					a{
						display: block;
						width: 30/@r;
						height: 30/@r;
					}
					a:first-child{
						background:url('../../assets/images/icon.png') no-repeat;
						background-position: -224/@r -108/@r;
						background-size: 720/@r;
						margin-left: 256/@r;
					}
					a:last-child{
						background:url('../../assets/images/icon.png') no-repeat;
						background-position: -288/@r -108/@r;
						background-size: 720/@r;
						margin-left: 30/@r;
					}
				}
			}
		}
		.pass-flag{
			position: absolute;
			left: 560/@r;
		    top: 0;
		    width: 80/@r;
		}
	}
</style>


